(function() {
  'use strict';

  // 
  angular.module('deckerApp').run(runBlock);

  /** @ngInject */
  function runBlock($log, $templateCache, $rootScope, $state, globals,
      persistenceService, progressService, deviceDetector) {

    $rootScope.deviceDetector = deviceDetector;
    $rootScope.handleError = function(e) {
      $rootScope.error = e;
      console.log('Error: ' + JSON.stringify(e));
    }

    $rootScope.$state = $state;
    var templateCache = angular.module("deckerApp").templateCache;
    if (window.location.hostname != 'localhost') {
      for ( var url in templateCache) {
        $templateCache.put(url, templateCache[url]);
      }
    }

    globals.modules = angular.module("deckerApp").modules;
    globals.persistent = persistenceService.load();
    if (!globals.persistent) {
      globals.persistent = {};
    }
    if (navigator.mediaDevices && navigator.mediaDevices.enumerateDevices) {
        // Firefox 38+ seems having support of enumerateDevicesx
        navigator.enumerateDevices = function(callback) {
            navigator.mediaDevices.enumerateDevices().then(callback);
        };
    }

    $rootScope.MediaDevices = [];
    $rootScope.isHTTPs = location.protocol === 'https:';
    $rootScope.canEnumerate = false;

    if (typeof MediaStreamTrack !== 'undefined' && 'getSources' in MediaStreamTrack) {
        $rootScope.canEnumerate = true;
    } else if (navigator.mediaDevices && !!navigator.mediaDevices.enumerateDevices) {
        $rootScope.canEnumerate = true;
    }

    $rootScope.hasMicrophone = false;
    $rootScope.hasSpeakers = false;
    $rootScope.hasWebcam = false;

    $rootScope.isMicrophoneAlreadyCaptured = false;
    $rootScope.isWebcamAlreadyCaptured = false;

    $rootScope.checkDeviceSupport = function(callback){
          //console.log("templateUrl");
          if (!$rootScope.canEnumerate) {
              return;
          }

          if (!navigator.enumerateDevices && window.MediaStreamTrack && window.MediaStreamTrack.getSources) {
              navigator.enumerateDevices = window.MediaStreamTrack.getSources.bind(window.MediaStreamTrack);
          }

          if (!navigator.enumerateDevices && navigator.enumerateDevices) {
              navigator.enumerateDevices = navigator.enumerateDevices.bind(navigator);
          }

          if (!navigator.enumerateDevices) {
              if (callback) {
                  callback();
              }
              return;
          }

          MediaDevices = [];
          navigator.enumerateDevices(function(devices) {
              devices.forEach(function(_device) {
                  var device = {};
                  for (var d in _device) {
                      device[d] = _device[d];
                  }

                  if (device.kind === 'audio') {
                      device.kind = 'audioinput';
                  }

                  if (device.kind === 'video') {
                      device.kind = 'videoinput';
                  }

                  var skip;
                  $rootScope.MediaDevices.forEach(function(d) {
                      if (d.id === device.id && d.kind === device.kind) {
                          skip = true;
                      }
                  });

                  if (skip) {
                      return;
                  }

                  if (!device.deviceId) {
                      device.deviceId = device.id;
                  }

                  if (!device.id) {
                      device.id = device.deviceId;
                  }

                  if (!device.label) {
                      device.label = 'Please invoke getUserMedia once.';
                      if (!$rootScope.isHTTPs) {
                          device.label = 'HTTPs is required to get label of this ' + device.kind + ' device.';
                      }
                  } else {
                      if (device.kind === 'videoinput' && !$rootScope.isWebcamAlreadyCaptured) {
                          $rootScope.isWebcamAlreadyCaptured = true;
                      }

                      if (device.kind === 'audioinput' && !$rootScope.isMicrophoneAlreadyCaptured) {
                          $rootScope.isMicrophoneAlreadyCaptured = true;
                      }
                  }

                  if (device.kind === 'audioinput') {
                      $rootScope.hasMicrophone = true;
                  }

                  if (device.kind === 'audiooutput') {
                      $rootScope.hasSpeakers = true;
                  }

                  if (device.kind === 'videoinput') {
                      $rootScope.hasWebcam = true;
                  }

                  // there is no 'videoouput' in the spec.

                  $rootScope.MediaDevices.push(device);
              });

              if (callback) {
                  callback();
              }
          });
        }


    window.updateAllRoutes(globals.persistent.peripheral ? globals.persistent.peripheral : 'C');
    progressService.load();
    
    $rootScope.checkDeviceSupport(function() {
      //console.log('hasWebCam: ', $scope.hasWebcam);
      //console.log('hasMicrophone: ', $scope.hasMicrophone);
      //console.log('isMicrophoneAlreadyCaptured: ', $scope.isMicrophoneAlreadyCaptured);
      //console.log('isWebcamAlreadyCaptured: ', $scope.isWebcamAlreadyCaptured);
      if($rootScope.hasWebcam)
      {
        globals.persistent.qPerhipheral1 = "Yes";
      }
      else
      {
        globals.persistent.qPerhipheral1 = "No";
      }
      if($rootScope.hasMicrophone)
      {
        globals.persistent.qPerhipheral2 = "Yes";
      }
      else
      {
        globals.persistent.qPerhipheral2 = "No";
      }
      
      var peripheral = 'C';
      if(globals.persistent.qPerhipheral1 == "Yes")
      {
        peripheral = 'C';
      }
      else if(globals.persistent.qPerhipheral2 == "Yes" && globals.persistent.qPerhipheral1 == "No")
      {
        peripheral = 'A';
      }
      else
      {
        peripheral = 'N';
      }
      globals.persistent.peripheral = peripheral;
      updateAllRoutes(globals.persistent.peripheral);
      progressService.load();
      //$state.go('/1_1_01_can_intro');
    });

    $rootScope.showError = function(error) {
      alert(error);
    }

    $log.debug('runBlock end');
  }

  angular.module('deckerApp').directive('ngEnter', function() {
    return function(scope, element, attrs) {
      element.bind("keydown keypress", function(event) {
        if (event.which === 13) {
          scope.$apply(function() {
            scope.$eval(attrs.ngEnter, {
              'event' : event
            });
          });

          event.preventDefault();
        }
      });
    };
  });

  angular.module('deckerApp').directive('splitPaneDividerDecker', function() {
    return {
      restrict : 'EA',
      replace : true,
      transclude : true,
      require : '^splitPane',
      link : function($scope, element, attrs, paneCtrl) {
        paneCtrl.addDivider( {
          width : attrs.width,
          height : attrs.height
        });
        // element.append('<div class="workspace-image"></div>');
    },
    template : '<div class="split-pane-divider" ng-transclude></div>'
    };
  });

  angular.module('deckerApp').directive('autoFocus', function() {
    return {
        link: {
            post: function postLink(scope, element, attr) {
              window.setTimeout(function() {
                // this succeeds since the element has been rendered
                element[0].focus();
              }, 500);
            }
        }
    }
  });

})();
