        $scope.$on('deckervideo.onPlay', function(event, status) {
          if (!status.player.pageUrl) {
            status.player.pageUrl = $state.current.url;
          }
          if (status.page && status.player.pageUrl == $state.current.url) {
            globals.isPlaying = true;
          }
        });

        $scope.$on('deckervideo.onPause', function(event, status) {
          if (!status.player.pageUrl) {
            status.player.pageUrl = $state.current.url;
          }
          if (status.page && status.player.pageUrl == $state.current.url) {
            globals.isPlaying = false;
          }
        });

        $scope.$on('deckervideo.onTime', function(event, status) {
          if (status.page) {
            if (!status.player.pageUrl) {
              status.player.pageUrl = $state.current.url;
            }
            if (status.player.pageUrl != $state.current.url) {
              return;
            }


            if(status.complete===1)
            {
                switch ($state.current.url) 
                {
                    case '/6_04__S.html':
                                         if(!globals.persistent.showPreS){
                                          globals.persistent.showPreS=1;
                                         }
                                         break;
                    case '/6_05__H.html':
                                         if(!globals.persistent.showPreH){
                                          globals.persistent.showPreH=1;
                                         }
                                         break; 

                    case '/6_06__A.html':
                                         if(!globals.persistent.showPreA){
                                          globals.persistent.showPreA=1;
                                          }
                                         break; 
                    case '/6_07__R.html':
                                         if(!globals.persistent.showPreR){
                                          globals.persistent.showPreR=1;
                                         }
                                         break; 
                    case '/6_08__P.html':
                                          if(!globals.persistent.showPreP){
                                            globals.persistent.showPreP=1
                                          }
                                          break;
                }
            }

            var pageProgress = progressService.setPageComplete(status.complete, status.position, status.duration);

            if (pageProgress) {
              globals.canFastForward = pageProgress.videoPos < pageProgress.maxViewed;
              globals.maxFastForward = pageProgress.maxViewed;
              //globals.canNextPage = progressService.canNextPage();
              globals.canNextPage = progressService.canNextPageByUrl();
            }
          }
        });

        $scope.$on('deckervideo.onComplete', function(event, status) {
          if (status.page) {
            progressService.setPageComplete();
            $scope.next();
          }
        });

