          obj.preventDefault();
          var downloadlink = document.getElementById('downloadlink');
          downloadlink.href = obj.currentTarget.href;
          var filename = downloadlink.href.substr(downloadlink.href.lastIndexOf('/') + 1)
          downloadlink.setAttribute('download',filename);
