        $scope.$root.getLastRecording = function(vrecorderId) {
          if (globals.persistent && globals.persistent.vrecorders && globals.persistent.vrecorders[vrecorderId]) {
            var recordings = globals.persistent.vrecorders[vrecorderId].recordings;
            if (!recordings) {
              recordings = globals.persistent.vrecorders[vrecorderId] = [];
            }
            if (recordings.length) {
              return recordings[recordings.length - 1];
            }
          }
          return null;
        }

        $scope.$root.clearRecordings = function(vrecorderId) {
          globals.persistent = globals.persistent || {};
          globals.persistent.vrecorders = globals.persistent.vrecorders || {};
          globals.persistent.vrecorders[vrecorderId] = {
              recordings : []
          };
          $scope.$root.getRecordingStatus();
          globals.redoRecording = false;
        }

