        var hBorder = 25;
        var unscaledPageWidth = UNSCALED_PAGE_WIDTH;
        var unscaledPageHeight = UNSCALED_PAGE_HEIGHT;
        var moduleVerticalToolbarWidth = 165;

        function scaleAndPositionPage() {
          var mainContainer = $('.main-container');
          var contentContainer = $('.content-container');
          var pageHolder = $('.page-holder');

          var contentWidth = $('.content-container').width();
          var contentHeight = contentContainer.height();

          $scope.$root.moduleFontSize = 
            Math.max(18, Math.min(24,
                $('.workspace-container').height() / 20
                ));
          
          var scalingX = contentWidth / unscaledPageWidth;
          var scalingY = contentHeight / unscaledPageHeight;
          var scaling = Math.min(scalingX, scalingY);
          $scope.$root.scaling = globals.scaling = scaling;

          var scaledWidth = unscaledPageWidth * scaling;
          var scaledHeight = unscaledPageHeight * scaling;

          var left = (contentWidth - scaledWidth) / 2;
          var top = (contentHeight - scaledHeight) / 2;
          var width = unscaledPageWidth;
          var height = unscaledPageHeight;

          pageHolder.css( {
            'left' : (left + 'px'),
            'top' : (top + 'px'),
            'width' : (width + 'px'),
            'height' : (height + 'px'),
            'transform' : ('scale(' + scaling + ')')
          });
        }

        function updateModuleToolbarButtons(moduleNavbarCSS) {
          var moduleToolbar = $('.module-navbar');
          var moduleButtons = moduleToolbar.find('.module-navbar-button');
          var numModules = 7; // moduleButtons.length

          if ($scope.viewStates.verticalModules) {
            var moduleButtonHeight = parseFloat(moduleNavbarCSS.height) / numModules;
            var moduleButtonTop = 0;

            // alert('moduleNavbarCSS.height = ' +
            // moduleNavbarCSS.height + ', moduleButtonHeight = ' +
            // moduleButtonHeight);
            moduleToolbar.find('.module-navbar-button').each(function(i, domElem) {
              var button = $(domElem);
              button.css( {
                position : 'absolute',
                width : (moduleVerticalToolbarWidth + 'px'),
                height : (moduleButtonHeight + 'px'),
                top : ((i * moduleButtonHeight) + 'px'),
                left : 0
              });
            });
          } else {
            var moduleButtonWidth = parseFloat(moduleNavbarCSS.width) / numModules;
            var moduleButtonHeight = 75;
            var moduleButtonTop = 0;
            var moduleButtonLeft = 0;

            moduleToolbar.find('.module-navbar-button').each(function(i, domElem) {
              var button = $(domElem);
              button.css( {
                position : 'absolute',
                width : (moduleButtonWidth + 'px'),
                height : (moduleButtonHeight + 'px'),
                top : 0,
                left : ((i * moduleButtonWidth) + 'px')
              });
            });
          }
        }

