(function () {
  'use strict';

  var sections = [];
  
  window.onFaqLoaded = function() {
    console.log("onFaqLoaded");
    var frameSrc = $('#faq-frame');
    var contents = frameSrc.contents(); 
    var titles = contents.find('.title');
    
    var children = contents.find('body').children();
    children.each(function(i, pElem) {
      var p = $(pElem);
      if (p.text().trim().length == 0) {
        return;
      }
      
      if (p.hasClass('title')) {
        sections.push({
          title: p.text(),
          qa: []
        });
      }
      else {
        if (sections.length == 0) {
          return;
        }
        var currentSection = sections[sections.length - 1];
        var text = p.text();
        if (text.indexOf('Q:') == 0) {
          currentSection.qa.push({
            question: text.substr(3)
          });
        }
        else {
          if (currentSection.qa.length == 0) {
            return;
          }
          var currentQA = currentSection.qa[currentSection.qa.length - 1];
          var a = p.html();
          a = a.replace('<span>A: ', '<span>').replace('<img alt="" src="images', '<img alt="" src="app/images/DeckerFAQ/images');
          if (! currentQA.answer) {
            currentQA.answer = '';
          }
          currentQA.answer += a;
          currentQA.collapsed = (currentSection.qa.length != 1);
        }
      }
    });
  }

  angular.module('deckerApp')
    .directive('compile', ['$compile', function ($compile) {
        return function(scope, element, attrs) {
            scope.$watch(
                function(scope) {
                    // watch the 'compile' expression for changes
                    return scope.$eval(attrs.compile);
                },
                function(value) {
                    // when the 'compile' expression changes
                    // assign it into the current DOM
                    element.html(value);

                    // compile the new DOM and link it to the current
                    // scope.
                    // NOTE: we only compile .childNodes so that
                    // we don't get into infinite loop compiling ourselves
                    $compile(element.contents())(scope);
                }
            );
        };
    }])
    .controller('FaqController', ['$scope', '$timeout', '$state', 'globals',
      function ($scope, $timeout, $state, globals) {
        globals.isFAQ = true;
        $scope.mainNavButtons = [];
        $scope.sections = sections;

        $timeout(function(){
            $(".faq-content-title").css('color','#337ab7'); 
            $('.faq-content-content.in').siblings(".faq-content-title").css('color','#FE961C'); 
        },100);
        if(!globals.selectedSection)
        {
          globals.selectedSection = 0;
          $timeout(function(){
            $(".faq-content-title").css('color','#337ab7'); 
            $('.faq-content-content.in').siblings(".faq-content-title").css('color','#FE961C'); 
          },100);
        }
        $scope.onBack = function() {
          
          // special case for returning from setup pages
          if (globals.modalsHistory.length > 0 && globals.modalsHistory[globals.modalsHistory.length-1].indexOf('1_03') != -1) {
            var newState = globals.modalsHistory.pop();
            //globals.modalsHistory = [];
            $state.go(newState);
            
          }
          else {
            if (globals.selectedSection == -1) {
              globals.isFAQ = false;
              $scope.$root.$broadcast('onModalBack');
            }
            else {
              globals.selectedSection = -1;
            }
          }
        }
        $scope.openSection = function(index) {
          globals.selectedSection = index;
           $timeout(function(){
            $(".faq-content-title").css('color','#337ab7'); 
            $('.faq-content-content.in').siblings(".faq-content-title").css('color','#FE961C'); 
          },100);
        }
        $scope.collapseSection = function(iQA) {
          var section = $scope.sections[globals.selectedSection];
          for (var i = 0; i < section.qa.length; i++) {
            section.qa[i].collapsed = (i != iQA);
          }
          $timeout(function(){
            $(".faq-content-title").css('color','#337ab7'); 
            $('.faq-content-content.in').siblings(".faq-content-title").css('color','#FE961C'); 
            globals.isFAQ = true;
          },100);
        }
    }
  ]);

})();
