(function() {
  'use strict';

  var sectionToTranscript = {};
  var currentSection;
  var timeout;
  var state;
  var scope;
  
  function updateTranscript() {
    var url = state.current.url;
    var toStateName = url.substr(1).replace('.html', '');
    if(toStateName == "6_04__S" || toStateName == "6_05__H" || toStateName == "6_06__A" || toStateName == "6_07__R" || toStateName == "6_08__P")
    {
      toStateName = toStateName+"-video";
    }
    if (sectionToTranscript.hasOwnProperty(toStateName)) {
      scope.transcriptHtml =      
        sectionToTranscript[toStateName];
    }
    else {
      scope.transcriptHtml =
        '<div class="no-transcript-msg-parent">' +
        '<span class="c0 no-transcript-msg">' +
        'No transcript here for now.<br>' +
        'But don\'t worry!<br>' +
        'It\'ll be back when we start talking again.' +
      '</span></div>';
    }

    timeout(function() {});
  }
  
  window.onTranscriptLoaded = function() {

    var frameSrc = $('#transcript-frame');
    var contents = frameSrc.contents();

    var modules = angular.module("deckerApp").modules;
    var moduleIndex;
    
    contents.find('body').children().each(function(i, pElem) {
      var p = $(pElem);
      var text = p.text().trim();

      if (text.indexOf('-video') == (text.length - 6)) {
        var segments = text.split('_');
        if (segments[1].length == 1) {
          segments[1] = '0' + segments[1];
        }
        text = segments.join('_');
        currentSection = text;

        moduleIndex = parseInt(segments[0]) - 1;
        sectionToTranscript[currentSection] = ''; 
      }
      else if (currentSection) {
        var html = p.html();
        if ((sectionToTranscript[currentSection].length > 0) || (text.trim().length > 0)) {
          if (sectionToTranscript[currentSection].length == 0) {
            html = '<p class="transcript-header">TRANSCRIPT</p>' + 
          '<p class="transcript-module-header">Module ' + (moduleIndex + 1) + ': ' + modules[moduleIndex].name + '</p>' + html;
          }
          sectionToTranscript[currentSection] += html;
        }
      }
    });
    
    updateTranscript();
  }

  angular.module('deckerApp').controller('TranscriptController',
      [ '$scope', '$timeout', '$state', function($scope, $timeout, $state) {

        timeout = $timeout;
        state = $state;
        scope = $scope;
        
        $scope.$on('$stateChangeSuccess', function(event, toState, toParams, fromState, fromParams) {
          updateTranscript();
        });        
      }]);

})();
