              var scroller_width = ($(".wav-analysis-graph-scroller").width()/2);
              //scleft = scrollwidth * scroll_width/100;  
              scleft = Math.floor(persec/scroller_width);  
              //$(".wav-analysis-graph-scroller").animate({scrollLeft:(scroller_width*scleft)},100);

              var w = scrollwidth + '%';
              var scroll_width = $(".wav-analysis-graph").width() - 850;
              //console.log("w:"+w);
