/**
 * @ngdoc directive
 * @name countdown
 * @description # videoFlagging
 */
angular.module('deckerApp').directive('countdown', function($timeout, globals) {
  return {
    templateUrl : 'app/scripts/directives/countdown.html',
    restrict : 'E',
    link : function postLink(scope, element, attrs) {
      function tick() {
        --globals.countdown;
        if (globals.countdown <= 0) {
          globals.startRecording = 1;
          globals.recordingStartTime = 0;
        } else {
          $timeout(tick, 1000);
        }
      }

      scope.globals = globals;
      scope.$watch('globals.recordingStartTime', function(v) {
        if (globals.persistent.peripheral == 'A') {
          if (v) {
            $timeout(function() {
              globals.startRecording = 1;
              globals.recordingStartTime = 0;
            }, 50);
           }
        }
        else if (v) {
          globals.countdown = 10;
          $timeout(tick, 1000);
        } else {
          globals.countdown = 0;
        }
      });
    }
  }
});
